import { useState } from "react";
import { ChevronDown, ChevronUp, Upload } from "lucide-react";
import { FileList } from "./FileList";
import { useProjectFiles } from "../lib/use-project-files";
import { FileVersion } from "@/entities/project/model/types";

interface StageProps {
  projectId: number;
  title: string;
  description: string;
  deadline: string;
  status: "completed" | "in_progress" | "waiting";
  supervisorComment?: string;
  defaultExpanded?: boolean;
}

export const Stage = ({
  projectId,
  title,
  description,
  deadline,
  status,
  supervisorComment,
  defaultExpanded = false,
}: StageProps) => {
  const [isExpanded, setIsExpanded] = useState(defaultExpanded);
  const { fileVersions, isLoading, uploadFile, downloadFile } =
    useProjectFiles(projectId);

  const handleFileUpload = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const file = event.target.files?.[0];
    if (!file) return;

    try {
      await uploadFile({ file });
    } catch (error) {
      console.error("Error uploading file:", error);
    }
  };

  const handleDownload = async (version: FileVersion) => {
    try {
      await downloadFile(version.id, version.fileName);
    } catch (error) {
      console.error("Error downloading file:", error);
    }
  };

  const getStatusColor = () => {
    switch (status) {
      case "completed":
        return "bg-green-100 text-green-800";
      case "in_progress":
        return "bg-blue-100 text-blue-800";
      case "waiting":
        return "bg-gray-100 text-gray-800";
    }
  };

  const getStatusText = () => {
    switch (status) {
      case "completed":
        return "Этап завершен";
      case "in_progress":
        return "В процессе";
      case "waiting":
        return "Ожидает";
    }
  };

  return (
    <div className="border rounded-lg bg-white overflow-hidden">
      <div
        className="flex items-center justify-between p-4 cursor-pointer"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <div className="flex items-center gap-4">
          <div>
            <h3 className="font-medium text-lg">{title}</h3>
            <p className="text-sm text-gray-500">
              Дедлайн: {new Date(deadline).toLocaleDateString()}
            </p>
          </div>
          <span
            className={`px-2 py-1 rounded-full text-xs ${getStatusColor()}`}
          >
            {getStatusText()}
          </span>
        </div>
        {isExpanded ? (
          <ChevronUp className="w-5 h-5 text-gray-400" />
        ) : (
          <ChevronDown className="w-5 h-5 text-gray-400" />
        )}
      </div>

      {isExpanded && (
        <div className="p-4 border-t">
          <p className="text-gray-600 mb-4">{description}</p>

          {supervisorComment && (
            <div className="mb-4 p-3 bg-blue-50 rounded-lg">
              <h4 className="font-medium mb-1">Отзыв руководителя</h4>
              <p className="text-gray-600">{supervisorComment}</p>
            </div>
          )}

          <div className="mb-4">
            <label
              htmlFor={`file-upload-${title}`}
              className="flex items-center gap-2 px-4 py-2 bg-primary text-white rounded-lg cursor-pointer hover:bg-primary/90 transition-colors inline-block"
            >
              <Upload className="w-4 h-4" />
              Загрузить файл
              <input
                id={`file-upload-${title}`}
                type="file"
                className="hidden"
                onChange={handleFileUpload}
              />
            </label>
          </div>

          <FileList
            files={fileVersions}
            isLoading={isLoading}
            onDownload={handleDownload}
          />
        </div>
      )}
    </div>
  );
};
