import { FileVersion } from "@/entities/project/model/types";
import { formatFileSize } from "@/shared/lib/format";
import { CheckCircle2, Clock, Download, Trash2, Upload } from "lucide-react";

interface FileListProps {
  files: FileVersion[];
  isLoading: boolean;
  onDownload: (version: FileVersion) => void;
  onDelete?: (version: FileVersion) => void;
  showStatus?: boolean;
}

export const FileList = ({
  files,
  isLoading,
  onDownload,
  onDelete,
  showStatus = true,
}: FileListProps) => {
  if (isLoading) {
    return <div className="text-gray-500">Загрузка...</div>;
  }

  if (files.length === 0) {
    return <div className="text-gray-500">Нет загруженных файлов</div>;
  }

  return (
    <div className="space-y-2">
      {files.map((file) => (
        <div
          key={file.id}
          className="flex items-center justify-between p-3 bg-white rounded-lg border border-gray-200"
        >
          <div className="flex items-center gap-3">
            <Upload className="w-5 h-5 text-gray-400" />
            <div>
              <div className="font-medium">{file.fileName}</div>
              <div className="text-sm text-gray-500">
                {formatFileSize(file.filePath.length)} •{" "}
                {new Date(file.uploadedAt).toLocaleDateString()}
              </div>
            </div>
          </div>

          <div className="flex items-center gap-4">
            {showStatus && (
              <div className="flex items-center gap-1 text-sm">
                {file.comment === "Одобрен" ? (
                  <>
                    <CheckCircle2 className="w-4 h-4 text-green-500" />
                    <span className="text-green-500">Одобрен</span>
                  </>
                ) : (
                  <>
                    <Clock className="w-4 h-4 text-yellow-500" />
                    <span className="text-yellow-500">На проверке</span>
                  </>
                )}
              </div>
            )}

            <div className="flex items-center gap-2">
              <button
                onClick={() => onDownload(file)}
                className="p-2 hover:bg-gray-100 rounded-full transition-colors"
              >
                <Download className="w-4 h-4 text-gray-500" />
              </button>
              {onDelete && (
                <button
                  onClick={() => onDelete(file)}
                  className="p-2 hover:bg-gray-100 rounded-full transition-colors"
                >
                  <Trash2 className="w-4 h-4 text-gray-500" />
                </button>
              )}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};
