"use client";

import { useState } from "react";
import { FileVersion, UploadFileDto } from "@/entities/project/model/types";
import { ProjectFilesService } from "../api/project-files.service";

export const useProjectFiles = (projectId: number) => {
  const [fileVersions, setFileVersions] = useState<FileVersion[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const loadFileVersions = async () => {
    try {
      setIsLoading(true);
      setError(null);
      const versions = await ProjectFilesService.getFileVersions(projectId);
      setFileVersions(versions);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Произошла ошибка");
    } finally {
      setIsLoading(false);
    }
  };

  const uploadFile = async (data: UploadFileDto) => {
    try {
      setIsLoading(true);
      setError(null);
      const newVersion = await ProjectFilesService.uploadFile(projectId, data);
      setFileVersions((prev) => [...prev, newVersion]);
      return newVersion;
    } catch (err) {
      setError(err instanceof Error ? err.message : "Произошла ошибка");
      throw err;
    } finally {
      setIsLoading(false);
    }
  };

  const downloadFile = async (versionId: number, fileName: string) => {
    try {
      setIsLoading(true);
      setError(null);
      const blob = await ProjectFilesService.downloadFile(versionId);

      // Create a download link
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", fileName);
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Произошла ошибка");
      throw err;
    } finally {
      setIsLoading(false);
    }
  };

  return {
    fileVersions,
    isLoading,
    error,
    loadFileVersions,
    uploadFile,
    downloadFile,
  };
};
