"use client";

import { API_BASE_URL } from "@/shared/constants/config";
import {
  FileVersion,
  FileVersionResponse,
  UploadFileDto,
} from "@/entities/project/model/types";

export class ProjectFilesService {
  static async uploadFile(
    projectId: number,
    data: UploadFileDto
  ): Promise<FileVersion> {
    const token = localStorage.getItem("authToken");
    if (!token) {
      throw new Error("Не авторизован");
    }

    const formData = new FormData();
    formData.append("file", data.file);
    if (data.comment) {
      formData.append("comment", data.comment);
    }

    const response = await fetch(
      `${API_BASE_URL}/projects/${projectId}/files`,
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: formData,
      }
    );

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || "Ошибка при загрузке файла");
    }

    const fileVersionResponse: FileVersionResponse = await response.json();
    return this.mapFileVersionResponse(fileVersionResponse);
  }

  static async getFileVersions(projectId: number): Promise<FileVersion[]> {
    const token = localStorage.getItem("authToken");
    if (!token) {
      throw new Error("Не авторизован");
    }

    const response = await fetch(
      `${API_BASE_URL}/projects/${projectId}/files`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || "Ошибка при получении версий файлов");
    }

    const fileVersionsResponse: FileVersionResponse[] = await response.json();
    return fileVersionsResponse.map(this.mapFileVersionResponse);
  }

  static async getFileVersion(versionId: number): Promise<FileVersion> {
    const token = localStorage.getItem("authToken");
    if (!token) {
      throw new Error("Не авторизован");
    }

    const response = await fetch(
      `${API_BASE_URL}/projects/files/${versionId}`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || "Ошибка при получении версии файла");
    }

    const fileVersionResponse: FileVersionResponse = await response.json();
    return this.mapFileVersionResponse(fileVersionResponse);
  }

  static async downloadFile(versionId: number): Promise<Blob> {
    const token = localStorage.getItem("authToken");
    if (!token) {
      throw new Error("Не авторизован");
    }

    const response = await fetch(
      `${API_BASE_URL}/projects/files/${versionId}/download`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(errorText || "Ошибка при скачивании файла");
    }

    return await response.blob();
  }

  private static mapFileVersionResponse(
    response: FileVersionResponse
  ): FileVersion {
    return {
      id: response.id,
      projectId: response.project_id,
      fileName: response.file_name,
      filePath: response.file_path,
      versionNumber: response.version_number,
      uploadedById: response.uploaded_by_id,
      uploadedAt: response.uploaded_at,
      comment: response.comment,
    };
  }
}
